* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.usersPage {
	width: 100%;
	height: 1000px;
	background-color: rgb(248, 248, 248);
	display: flex;
	flex-direction: column;
	align-items: center;

	.container {
		margin-top: 100px;
		width: 90%;
		text-transform: capitalize;

		.toolbar {

			button {
				border-radius: 5px;
				text-transform: capitalize;
				width: 120px;
				background-color: rgb(228, 228, 228);
				cursor: pointer;
			}
		}


	}
}